.create__post__link__container {
  position: fixed;
  bottom: 15px;
  right: 20px;
}
.create__post__link__container a {
  text-decoration: none;
  background-color: var(--general-color);
  padding: 0.85em 1.1em;
  border-radius: 50%;
  display: block;
  box-shadow: 0 4px 10px 2px rgba(0, 0, 0, 0.5);
  transition: transform 150ms linear;
}
.create__post__link__container a i {
  color: var(--color-white);
  font-size: 1.45em;
}
.create__post__link__container a:hover, .create__post__link__container a:focus {
  transform: scale(0.96);
}

/************************
PROFILE SUB SECTION 2
*************************/
.profile--main--container {
  width: 100%;
  max-width: 840px;
  margin: 0 auto;
  background-color: var(--home-logged-bg);
  font-family: "Poppins", sans-serif;
}
.profile__sub__container__one {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
.profile__header__img__container {
  width: 100%;
  height: 20em;
}
.profile__header__img__container img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.profile__avatar__img__container {
  position: relative;
}
.profile__avatar__img__container .avatar__container {
  text-align: Center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.profile__avatar__img__container .avatar__container h2 {
  font-family: "Nunito", sans-serif;
  color: var(--h1-color);
  line-height: 1.1;
}
.profile__avatar__img__container .avatar__container p {
  color: var(--general-color);
  padding-bottom: 1em;
}
.profile__navigation {
  top: 3.5em;
  right: -13em;
  position: absolute;
  background-color: var(--color-white);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 1.5em 0.75em;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.35);
  border-radius: 0.5em;
}
.profile__navigation__hidden {
  display: none;
}
.profile__navigation__link {
  margin-bottom: 0.4em;
  padding: 0.2em 0.1em;
  letter-spacing: 0.03em;
  display: inline-block;
  text-decoration: none;
  color: var(--general-color);
  font-size: 1em;
  font-weight: 600;
  text-transform: uppercase;
}
.profile__navigation__link:hover, .profile__navigation__link:focus {
  opacity: 0.65;
}
.profile__navigation__link i {
  margin-right: 0.2em;
}
.profile__navigation__link:last-child {
  margin-bottom: 0;
}

.avatar__container a {
  height: 11em;
  width: 11em;
  display: inline-block;
  text-decoration: none;
  border-radius: 50%;
  border: 5px solid var(--color-white);
  background-color: var(--color-white);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
}
.avatar__container a img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  border-radius: 50%;
}

.profile__btn {
  position: absolute;
  top: 1em;
  right: -8.5em;
  background-color: transparent;
  outline: none;
  border: none;
  border-radius: 0.35em;
}
.profile__btn i {
  font-size: 1.5em;
  padding: 0.25em 0.35em;
  color: var(--general-color);
}
.profile__btn:hover i {
  background-color: #eae6ff;
  cursor: pointer;
}

/***************************
PROFILE SUB CONTAINER TWO
***************************/
.profile__sub__container__two {
  margin-top: 7em;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 0.75em;
  padding: 3em 2em;
  color: var(--profile-para-color);
  background-color: var(--color-white);
}
.profile__sub__container__two i {
  margin-right: 0.5em;
}

.join__date__container {
  color: #3f3a43;
}

.follow__details__container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.follow__details__container .followers__btn {
  margin-right: 1em;
}
.follow__details__container a {
  color: var(--h1-color);
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  display: block;
  font-weight: 700;
  font-size: 1em;
}
.follow__details__container a:hover, .follow__details__container a:focus {
  opacity: 0.7;
  cursor: pointer;
}

/**************************
PROFILE USER POSTS SECTION
*************************/
.profile__user__posts {
  max-width: 540px;
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 2em;
}
.profile__user__posts__container {
  padding: 2em 0;
}
.profile__user__posts article {
  width: 100%;
  background-color: var(--color-white);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  border-radius: 0.5em;
  display: grid;
  grid-template-columns: 1fr;
}
.profile__user__posts article:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.35);
}

.post {
  /***************************
  POST AUTHOR RELATED STYLING
  ***************************/
  /****************************
  POST BUTTONS RELATED STYLING
  *****************************/
}
.post__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  padding: 1em;
}
.post__author__container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.post__author__img__wrapper a {
  text-decoration: none;
  display: block;
  margin-right: 0.6em;
  width: 3em;
  height: 3em;
  border-radius: 50%;
}
.post__author__img__wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 50%;
  border: 2px solid var(--general-color);
}
.post__author__name__container div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.post__author__name__container div a {
  text-decoration: none;
  display: block;
}
.post__author__name__container .author__name {
  font-size: 0.9em;
  color: var(--h1-color);
  line-height: 1.3;
}
.post__author__name__container .author__name:hover, .post__author__name__container .author__name:focus {
  opacity: 0.65;
}
.post__author__name__container span {
  line-height: 1.2;
  font-size: 0.7em;
  display: block;
  color: #494c59;
  font-family: "Ubuntu", sans-serif;
}
.post__related__btn__links__container {
  position: relative;
}
.post__related__btn__links__container .sideNavShowBtn,
.post__related__btn__links__container .sideNavHideBtn {
  background: transparent;
  border: none;
  cursor: pointer;
}
.post__related__btn__links__container .sideNavShowBtn i,
.post__related__btn__links__container .sideNavHideBtn i {
  padding: 0.15em;
}
.post__related__btn__links__container div {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  position: absolute;
  top: -1em;
  right: -1em;
  width: 8em;
  box-shadow: -4px 4px 6px rgba(0, 0, 0, 0.3);
  padding: 2em 0.75em 1em;
  display: none;
  background-color: var(--color-white);
  border-radius: 0.3em;
}
.post__related__btn__links__container div a {
  font-size: 0.9em;
  text-decoration: none;
  display: block;
  color: var(--general-color);
}
.post__related__btn__links__container div a i {
  margin-right: 0.2em;
}
.post__related__btn__links__container div a:hover, .post__related__btn__links__container div a:focus {
  opacity: 0.7;
  cursor: pointer;
}
.post__related__btn__links__container div > a {
  margin-bottom: 0.25em;
}
.post__related__btn__links__container div .sideNavHideBtn {
  position: absolute;
  top: 0.75em;
  right: 1em;
}

/***************************
POST ASSETS
***************************/
.post__content__container {
  padding: 0 1em;
}
.post__content__container a {
  text-decoration: none;
  display: block;
  color: var(--label-color);
  font-family: "Noto Sans", sans-serif;
  line-height: 1.5;
  font-size: 1em;
  margin-bottom: 0.5em;
}
.post__image__container a {
  text-decoration: none;
  display: block;
}
.post__image__container img {
  width: 100%;
  display: block;
}

/************************
LIKE AND COMMENT BTNS
*************************/
.post__footer {
  border-top: 1px solid var(--input-border-color);
  padding: 0.5em;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

.heart__btn,
.comment__btn {
  text-decoration: none;
  display: block;
  color: #3f3d4f;
  border: none;
  background-color: transparent;
  font-size: 1.5em;
  cursor: pointer;
}
.heart__btn span,
.comment__btn span {
  font-size: 0.85em;
  font-family: "Noto Sans", sans-serif;
}

.heart__btn__active {
  color: var(--grad-color-1);
}

/**************************
MINOR STYLING FOR PAGINATION
**************************/
.pagination__section {
  padding: 0 1em 2em 0;
  max-width: 840px;
}

/***********************
MEDIA QUERIES
************************/
@media (min-width: 720px) {
  .profile__btn {
    right: -8.5em;
  }
}